header {
  height: 100vh;
  padding: 4rem 0;
  overflow: hidden;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

/* ===== CTA Styles ===== */
.cta {
  margin-top:2.5rem;
  display:flex;
  gap:1.2rem;
  justify-content: center;
}

/* ===== Header Socials Styles ===== */
.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 5rem;
}

.header__socials::after {
  content: '';
  width:1px;
  height:2rem;
  background: var(--color-primary);
}

/* ===== Profile picture styles ===== */
.profile-picture {
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 30rem;
  position: absolute;
  left: calc(50% - 11rem);
  margin-top: 4rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 5rem 1.5rem 1.5rem 1.5rem;
  animation: profile__animate 8s ease-in-out 1s infinite;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: inset 0 0 0 9px hsl(0deg 0% 88% / 30%);
  height: 300px;
  justify-self: center;
  order: 1;
  width: 300px;
  filter:grayscale()
}

.profile-picture > img {
  position:relative;
  bottom:30px;
}

@keyframes profile__animate {
  0% {
      border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }
  50% {
      border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
  }
  100% {
      border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }
}

/* ===== Scroll Down ===== */
.scroll__down {
  position:absolute;
  right: -2.3rem;
  bottom:8rem;
  transform: rotate(90deg);
  font-weight:300;
  font-size:0.9rem;
}

/* MEDIA QUERIES - MEDIUM DEVICES */
@media screen and (max-width: 1024px) {
  header {
    height:68vh;
  }
}

/* MEDIA QUERIES - SMALL DEVICES */
@media screen and (max-width: 600px) {
  header {
    height:100vh;
  }
  .header__socials,
  .scroll__down {
    display: none;
  }
}


